/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
}

.section {
  padding: 2rem 0;
}

h1 {
  font-size: "Staatliches", cursive;
}

.heading--h2 {
  font-family: "Staatliches", cursive;
  text-align: center;
  font-size: 5rem;
  letter-spacing: 5px;
  margin-bottom: 10rem;
  font-weight: 400;
}
.heading--h2 span {
  font-weight: 700;
}
.heading--h2::after {
  content: "";
  margin: 0 auto;
  display: block;
  height: 1rem;
  width: 30rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #e8505b 0%, #e8505b 50%, #f4aaaf 50%, #f4aaaf 100%);
}

.title--h3 {
  font-family: "Raleway", sans-serif;
  text-align: center;
  font-size: 5rem;
  letter-spacing: 5px;
  margin-bottom: 10rem;
  font-weight: 400;
}
.title--h3 span {
  font-weight: 700;
}
.title--h3::after {
  content: "";
  margin: 0 auto;
  display: block;
  height: 1rem;
  width: 30rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #e8505b 0%, #e8505b 50%, #f4aaaf 50%, #f4aaaf 100%);
}
.title--h3:after {
  background-image: linear-gradient(to right, #6c63ff 0%, #6c63ff 50%, #ccc9ff 50%, #ccc9ff 100%);
  width: 20rem;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../img/headset.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  padding: 5rem 0 10rem 0;
}
.header__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  gap: 4.5rem;
}
.header__title {
  margin: 0;
  color: #FFFFFF;
  text-align: center;
  font-size: 5rem;
  font-weight: 700;
}
.header__text {
  text-align: center;
  margin: 0;
  color: #FFFFFF;
}
.header__button {
  grid-row: 2/3;
  width: 100%;
  text-align: center;
  font-size: 2rem;
  font-weight: 400;
  color: #FFFFFF;
  padding: 1rem 3rem;
  border: 2px solid white;
  border-radius: 8px;
  transition: background-color 0.3s;
  width: 100%;
}
.header__button:hover {
  background-color: #fcfcfc;
  border: 2px solid white;
  color: #000;
}
@media (min-width: 768px) {
  .header__button {
    width: 20%;
  }
}

.nav {
  display: flex;
  justify-content: center;
  gap: 5rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 10rem;
  }
}
.nav__link {
  color: #cdcdcd;
  font-size: 2rem;
}
.nav__link:hover {
  color: #FFFFFF;
}
.nav__link--active {
  color: #FFFFFF;
}

.main__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 10rem;
}

.main__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .main__card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.main__card:nth-child(even) .main__card-info {
  grid-column: 1/2;
  grid-row: 1/2;
}
.main__card:nth-child(even) .main__card-image {
  grid-column: 2/3;
}
.main__card-image {
  width: 35rem;
}
@media (min-width: 768px) {
  .main__card-image {
    width: 55rem;
  }
}
.main__card-info {
  text-align: center;
}
.main__card-title {
  margin-bottom: 3rem;
  font-size: 4rem;
  font-weight: 700;
}
.main__card-text {
  margin: 0;
  text-align: center;
  line-height: 1.8;
  padding-inline: 6rem;
  font-size: 2rem;
  font-weight: 400;
  color: gray;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  align-items: center;
  /* Si el placeholder no esta activo*/
  /* Si focus esta activo su hermano directo se afecta*/
}
@media (min-width: 768px) {
  .form {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.form__group {
  position: relative;
  color: #5757577e;
}
.form__input {
  width: 30rem;
  background: none;
  color: #706c6c;
  font-size: 1.8rem;
  padding: 0.6rem 1rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #5757577e;
}
@media (min-width: 768px) {
  .form__input {
    width: 40rem;
  }
}
.form__input:not(:placeholder-shown) {
  color: #f4aaaf;
}
.form__input:focus + .form__label, .form__input:not(:placeholder-shown) + .form__label {
  transform: translateY(-20px) scale(0.7);
  transform-origin: left top;
  font-size: 2.3rem;
  font-weight: 700;
  color: #e8505b;
}
.form__label {
  color: #5757577e;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 5px;
  transform: translateY(10px);
  transition: transform 0.5s, color 0.3s;
}
.form__line {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  transform: scale(0);
  transform: left bottom;
  transition: transform 0.4s;
}
.form__input:focus ~ .form__line, .form__input:not(:placeholder-shown) ~ .form__line {
  transform: scale(1);
  background-color: #e8505b;
}
.form__button {
  grid-row: 2/3;
  width: 100%;
  text-align: center;
  font-size: 2rem;
  font-weight: 400;
  color: #ee7d85;
  padding: 1rem 3rem;
  border: 2px solid #f4aaaf;
  border-radius: 8px;
  transition: background-color 0.3s;
  background-color: #FFFFFF;
  width: auto;
}
.form__button:hover {
  background-color: #e74b57;
  border: 2px solid #ee7d85;
  color: #FFFFFF;
}

.form--email {
  display: flex;
  flex-direction: column;
  gap: 10rem;
}
.form__area {
  height: 10rem;
  resize: none;
}
.form__button--email {
  margin-bottom: 5rem;
}

.search__result {
  margin-top: 5rem;
}
.search__result-text {
  text-align: center;
  line-height: 1.5;
  white-space: pre-wrap;
}
.search__result-spinner {
  display: flex;
  justify-content: center;
}
.search__result h3 {
  font-weight: 400;
}
.search__result h3 span {
  font-weight: 700;
}
.search__result h3::after {
  content: "";
  margin: 0 auto;
  display: block;
  height: 1rem;
  width: 30rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #e8505b 0%, #e8505b 50%, #f4aaaf 50%, #f4aaaf 100%);
}
.search__result h3:after {
  width: 30rem;
  background-image: linear-gradient(to right, #6c63ff 0%, #6c63ff 50%, #ccc9ff 50%, #ccc9ff 100%);
}

.d-none {
  display: none;
}

.spinner {
  margin: 100px auto 0;
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #6c63ff;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.main__message {
  position: fixed;
  background-color: #C8E6C9;
  color: #388E3C;
  right: 0;
  top: 5rem;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 2rem 4rem;
  border: 1px solid #66BB6A;
  font-size: 1.5rem;
  font-weight: 700;
  opacity: 0;
  animation-name: message;
  animation-duration: 3s;
  animation-delay: 0.5s;
  animation-timing-function: ease;
  animation-iteration-count: 1;
}
.main__message span {
  font-weight: 400;
}

@keyframes message {
  0% {
    opacity: 0;
    transform: translateX(30rem);
  }
  10% {
    opacity: 0.5;
    transform: translateX(0rem);
  }
  30% {
    opacity: 1;
    transform: translateX(0rem);
  }
  50% {
    opacity: 1;
    transform: translateX(0rem);
  }
  90% {
    opacity: 0.5;
    transform: translateX(30rem);
  }
  100% {
    opacity: 0;
    transform: translateX(30rem);
  }
}
.footer {
  background-color: #000;
}
.footer__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  padding: 3rem;
}
@media (min-width: 768px) {
  .footer__grid {
    justify-content: space-between;
    flex-direction: row;
    gap: 0;
  }
}
.footer__copyright {
  margin: 0;
  color: #FFFFFF;
}

.nav--position {
  margin: 0;
}